.todo {
  margin-bottom: 1rem;
}

.todo.completed span {
  text-decoration: line-through;
}

.todo label {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  padding: 1rem;
  background-color: #f3f3f3;
}

.todo i {
  z-index: 2;
}

.nav-wrapper a {
  font-size: 20px;
}

.todo-title {
  height: auto !important;
  word-break: break-all;
}